<template>
  <li :class="`tree-node ${model.index == 1 ? 'border-bottom' : ''} ${model.info == 'Play' ? 'list-unstyled' : ''}`">
    <div :class="`d-flex align-items-center text-capitalize onhover-primary ${model.index == 1 ? 'app-heading' : ''}`">
      <span class="toggle-icon mr-2" :class="{ 'empty-toggle': !isFolder }" :key="open" v-if="model.index != 1 && isFolder">
        <b-img src="/img/filled-chevron-icon.png" height="5" alt="logo" :class="{ 'rotate-0': this.open, 'rotate-270': !this.open }" />
      </span>
      <div :class="`cursor-pointer ${model.index == 1 ? 'font-weight-700' : ''} ${isFolder ? '' : ''}`">
        <input type="radio" class="node-text" name="rad" v-model="checkedWatch" :id="model.id" :value="model.id" />
        <label v-show="!edit" class="mb-0" :class="{ 'searched-text': isSearchText }" :for="model.id" @click="toggle" @contextmenu.prevent="showContextMenu" key="label">{{ model.text }}</label>
        <input v-show="edit" ref="title" class="mb-0" v-model="model.text" :placeholder="model.text" key="input" @blur="blur" @keyup.enter="blur" />
      </div>
      <i :class="{ 'fe fe-chevron-down text-muted': this.open, 'fe fe-chevron-right text-muted': !this.open }" class="ml-auto" v-if="model.index == 1" />
      <!--<span class="tree-icon" :class="{ 'empty-toggle': !icon }" :key="icon">
      <i v-if="icon" class="far" :class="icon"></i>
    </span>-->
    </div>

    <div class="tree-children">
      <ul v-show="open" v-if="isFolder" :class="'list-item-' + model.index">
        <v-treeview-item
          v-for="child in model.children"
          :key="child.id"
          :model="child"
          :treeTypes="treeTypes"
          :openAll="openAll"
          @addNode="addNode"
          @selected="selected"
          :searchText="searchText"
          @openTree="openTree"
        >
        </v-treeview-item>
      </ul>
    </div>
  </li>
</template>

<script>
  export default {
    name: "v-treeview-item",
    props: ["model", "treeTypes", "openAll", "searchText"],
    data() {
      return {
        open: false,
        checked: null,
        edit: false,
      };
    },
    computed: {
      checkedWatch: {
        get() {
          return this.checked || this.model.active;
        },
        set(newValue) {
          this.checked = newValue;
        },
      },
      isFolder() {
        return this.model.children && this.model.children.length;
      },
      icon() {
        return this.getTypeRule(this.model.type).icon;
      },
      isSearchText() {
        if (this.searchText && this.searchText != "") {
          if (this.model.text.toLowerCase().indexOf(this.searchText.toLowerCase()) !== -1) {
            this.openTree(this);
            return true;
          } else return false;
        }
        return false;
      },
    },
    methods: {
      getTypeRule(type) {
        var typeRule = this.treeTypes.filter((t) => t.type == type)[0];
        return typeRule;
      },
      blur() {
        this.edit = false;
      },
      selected(node) {
        this.checked = null;
        this.checked = this.model.id;
        this.$emit("selected", node);
      },
      openTree(node) {
        this.open = true;
        this.$emit("openTree", node);
      },
      addNode(newNode) {
        var typeRule = this.getTypeRule(this.model.type);

        if (typeRule.valid_children.indexOf(newNode.type) > -1) {
          this.model.children.push(newNode);
        }
      },
      editName() {
        this.edit = true;
        this.$nextTick(() => this.$refs.title.focus());
      },
      showContextMenu(e) {
        e.preventDefault();
        this.open = true;
        this.selected(this);
      },
      toggle() {
        if (this.isFolder) {
          this.open = !this.open;
        }
        this.selected(this);
      },
    },
    created() {
      if (this.model.id == null) {
        this.editName();
      }
      this.open = this.openAll;
    },
    watch: {
      openAll(openAll) {
        this.open = openAll;
      },
    },
  };
</script>
