function loadMenuJson() {
  const locales = require.context("../js", true, /[A-Za-z0-9-_,\s]+\.json$/i);
  let configJson = {};
  locales.keys().forEach((key) => {
    const matched = key.match(/([A-Za-z0-9-_]+)\./i);
    if (matched && matched.length > 1) {
      configJson[matched[1]] = locales(key);
    }
  });
  return configJson;
}

export default loadMenuJson;
