<template>
  <div class="d-lg-flex flex-md-column vh-100">
    <b-navbar type="dark" variant="white" class="border-bottom px-4" toggleable="lg">
      <b-navbar-brand>
        <b-img src="/img/500_apps_logo.svg" height="50" />
      </b-navbar-brand>
      <b-navbar-toggle target="is_mobile" class="navbar-toggler-icon ml-auto"></b-navbar-toggle>
    </b-navbar>
    <b-container class="flex-grow overflow-hidden mt-3 mt-lg-0 my-lg-auto" fluid>
      <b-row class="h-100">
        <b-col md="12" lg="12" xl="12" class="m-lg-auto px-md-4">
          <b-card class="mb-0 max-height-90vh" no-body>
            <div class="d-lg-none w-100 px-4 pt-4"></div>
            <b-row no-gutters class="h-100">
              <b-col class="border-right col-auto mobile-slideout">
                <b-collapse id="is_mobile">
                  <div class="float-right close-icon d-lg-none">
                    <b-link @click.stop="$root.$emit('bv::toggle::collapse', 'is_mobile')" class="close">
                      <i class="fe fe-x" />
                    </b-link>
                  </div>
                  <div class="lhs-width-300 category-lhs py-lg-3 py-4 h-100">
                    <Menu @link="play" />
                  </div>
                </b-collapse>
              </b-col>
              <b-col class="overflow-auto h-100">
                <b-card-body class="p-xl-5">
                  <div class="border rounded">
                    <iframe :src="link" :key="rerender" class="border-0" height="600" width="100%" style="max-width: 100%" v-if="enableIframe" />
                    <video width="100%" height="100%" preload="metadata" v-if="link && !enableIframe" controls id="target" :src="link" buffered autoplay />
                  </div>
                  <div class="mt-4" v-if="title || description">
                    <h2 class="mb-2">{{ title }}</h2>
                    <p class="mb-0 font-size-base">{{ description }}</p>
                  </div>
                </b-card-body>
              </b-col>
            </b-row>
          </b-card>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>
<script>
  import MenusJSON from "../js/MenuJson";
  import Menu from "./Menu.vue";
  export default {
    components: { Menu },
    data() {
      return {
        defaultMenu: {},
        link: "",
        enableIframe: false,
        rerender: 0,
        title: "",
        description: "",
        menuJson: {},
      };
    },
    mounted() {
      this.defaultMenu = MenusJSON();
      // Copy the object to menujson.
      this.menuJson = JSON.parse(JSON.stringify(this.defaultMenu));
      this.playVideo();
    },
    watch: {
      $route() {
        this.playVideo();
      },
    },
    methods: {
      playVideo() {
        // Get the title and description based on given route.
        let response = (this.defaultMenu[this.$route.params.appname] || {})[this.$route.params.category];
        if (response && this.$route.params.subcategory) response = this.defaultMenu[this.$route.params.appname][this.$route.params.category][this.$route.params.subcategory];
        if (response && !Object.keys(response).includes("link")) response = response[Object.keys(response)[0]];
        if (!response)
          // Load the first video if appname and description doesn't matches.
          response = this.getFirstLink();
        this.setConfig(response);
      },
      // Assign the link, title, description.
      play(value) {
        this.$nextTick(() => {
          this.rerender++;
        });
        this.setConfig(value);
      },
      // Get the link if any appname or category doesn't matches.
      getFirstLink() {
        let response = this.menuJson[Object.keys(this.menuJson)[0]];
        if (response && Object.keys(response).includes("link")) {
          return response;
        }
        this.menuJson = response;
        return this.getFirstLink();
      },
      // Assign the value.
      setConfig(response) {
        this.link = response.link;
        this.title = response.title;
        this.description = response.description;
        this.enableIframe = false;
        if (this.link.includes("vimeo.com")) this.enableIframe = true;
      },
    },
  };
</script>
