import MenuJson from "./MenuJson";

class Treeview {
  constructor() {
    this.initJs = MenuJson();
  }
  // Prepare the tree type json required for v-treeview component
  getTreeType() {
    let response = [
      {
        type: "#",
        max_children: 6,
        max_depth: 4,
        valid_children: Object.keys(this.initJs),
      },
    ];
    Object.keys(this.initJs).forEach((row) => {
      response.push({
        type: row,
        icon: "",
        valid_children: Object.keys(this.initJs[row]).map((children) => {
          let result = [];
          Object.keys(this.initJs[row][children]).forEach((subchild) => {
            if (subchild != "link" && subchild != "title" && subchild != "description") {
              response.push({ type: `${row}-${children}-${subchild}`, icon: "", valid_children: [`${row}-${children}-${subchild}-play`] });
              response.push({ type: `${row}-${children}-${subchild}-play`, icon: "", valid_children: [] });
              result.push(`${row}-${children}-${subchild}`);
            } else if (result.length < 1) {
              response.push({ type: `${row}-${children}-play`, icon: "", valid_children: [] });
              result.push(`${row}-${children}-play`);
            }
          });
          response.push({
            type: `${row}-${children}`,
            icon: "",
            valid_children: result,
          });
          return `${row}-${children}`;
        }),
      });
    });
    return response;
  }

  // Prepare the tree data json required for v-treeview component
  getTreeData(params) {
    let response = [];
    let count = 1;
    Object.keys(this.initJs).forEach((row, parnetIndex) => {
      response.push({
        text: row,
        type: row,
        id: 10 + parnetIndex,
        index: 1,
        children: Object.keys(this.initJs[row]).map((children, childrenIndex) => {
          let result = [];
          Object.keys(this.initJs[row][children]).forEach((subchild, subchildIndex) => {
            if (subchild != "link" && subchild != "title" && subchild != "description")
              result.push({
                id: 1000 + parnetIndex + childrenIndex + subchildIndex + ++count,
                text: subchild,
                type: `${row}-${children}-${subchild}`,
                index: 3,
                children: [
                  {
                    id: 10000 + parnetIndex + childrenIndex + subchildIndex + ++count,
                    active: `${row}-${children}-${subchild}` == `${params.appname}-${params.category}-${params.subcategory}` ? 10000 + parnetIndex + childrenIndex + subchildIndex + count : "",
                    text: this.initJs[row][children][subchild].title || "Click here",
                    index: 4,
                    type: `${row}-${children}-${subchild}-play`,
                    info: "Play",
                    children: [],
                  },
                ],
              });
            else if (result.length < 1)
              result.push({
                id: 1000 + parnetIndex + childrenIndex + subchildIndex + ++count,
                active: `${row}-${children}` == `${params.appname}-${params.category}` ? 1000 + parnetIndex + count + childrenIndex + subchildIndex : "",
                index: 3,
                text: this.initJs[row][children].title || "Click here",
                info: "Play",
                type: `${row}-${children}-play`,
                children: [],
              });
          });
          return {
            index: 2,
            text: children,
            id: 100 + parnetIndex + childrenIndex + ++count,
            type: `${row}-${children}`,
            children: result,
          };
        }),
      });
    });

    return response;
  }
}
export default new Treeview();
