<template>
  <div><VTreeview ref="treeViewRef" v-if="enable" v-model="treeData" :treeTypes="treeTypes" @selected="selected" :openAll="openAll"></VTreeview></div>
</template>
<script>
  import VTreeview from "./VTreeview.vue";
  import treeviewJs from "../js/treeview";
  export default {
    data() {
      return {
        openAll: true,
        treeTypes: [],
        treeData: [],
        contextItems: [],
        selectedNode: null,
        enable: false,
      };
    },
    mounted() {
      this.treeTypes = treeviewJs.getTreeType();
      this.treeData = treeviewJs.getTreeData(this.$route.params);
      this.$nextTick(() => {
        this.enable = true;
      });
    },
    methods: {
      getTypeRule(type) {
        var typeRule = this.treeTypes.filter((t) => t.type == type)[0];
        return typeRule;
      },
      selected(node) {
        let selectedNode = node.model.type;
        let defaultLink = selectedNode.split("-");
        if (defaultLink.length == 4) {
          // Close lhs menu when we click on category(For mobile view only).
          if (window.innerWidth < 990) this.$root.$emit("bv::toggle::collapse", "is_mobile");
          this.$router.push({ params: { appname: defaultLink[0], category: defaultLink[1], subcategory: defaultLink[2] } });
        }
        if (defaultLink.length == 3 && !node.model.children.length) {
          // Close lhs menu when we click on category(For mobile view only).
          if (window.innerWidth < 990) this.$root.$emit("bv::toggle::collapse", "is_mobile");
          this.$router.push({ params: { appname: defaultLink[0], category: defaultLink[1], subcategory: undefined } });
        }
      },
    },
    components: {
      VTreeview,
    },
  };
</script>
